.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer{
  margin-top: 1rem;
  padding: 1rem;
  background-color:#272727;
  position:relative;
  bottom: 0;
  left: 0;
  width: 100%;
  font-size: 12px;
}

.footer1{
  color: rgba(255, 255, 255, 0.7);
}

.card {
  align-items: center;
  justify-content: center;
  border: 2px solid rgb(147, 158, 207);
  border-radius: .75rem;
  border-top-right-radius: .75rem;
}

.card1 {
  text-align: center;
}